@import url('https://fonts.googleapis.com/css2?family=Black+Han+Sans&family=Diphylleia&family=Single+Day&display=swap');


body{
  background-color: #f6f6f6;
  display:flex;
  justify-content: center;
  align-items: center;
  font-family: 'Diphylleia', serif;
  min-height: 100vh;
  margin: 0px;
}

@media (min-width : 650px){
  .App{
    width:640px;
  }
}

@media (max-width : 650px){
  .App{
    width:90vw;
  }
}

#root{
  background-color: white;
  box-shadow : rgba(100, 100, 111, 0.2) 0px 7px 29px 0px
}

.App{
  min-height: 100vh;
  padding: 0px 20px 0px 20px;
}

/*  MyButton 스타일링 */
.MyButton{
  cursor : pointer;
  border: none;
  border-radius:5px;

  padding: 10px 20px 10px 20px;

  font-size: 14px;

  white-space: nowrap;
  font-family: 'Diphylleia', serif;
}

.MyButton_default{
  background-color: #ececec;
  color: black;
}

.MyButton_positive{
  background-color: #64c964;
  color: white;
}

.MyButton_negative{
  background-color: #fd565f;
  color:white;
}

/* 헤더 스타일링 */
header{
  padding: 20px 0px 20px 0px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #e2e2e2;
}

header > div{
  display: flex;
}

header .head_text{
  width: 50%;
  font-size: 25px;
  justify-content: center;
}

header .head_btn_left{
  width:25%;
  justify-content: start;

}

header .head_btn_right{
  width:25%;
  justify-content: end;
}

header button{
  font-family: 'Single Day', cursive;
}

/* DiaryList 스타일링 */
.DiaryList .menu_wrapper{
  margin-top : 20px;
  margin-bottom: 30px;

  display: flex;
  justify-content: space-between;
}

.DiaryList .menu_wrapper .right_col{
  flex-grow: 1;
}

.DiaryList .menu_wrapper .right_col button{
  width: 100%;
}

.DiaryList .ControlMenu {
  margin-right: 10px;
  border: none;
  border-radius:  5px;
  background-color: #ececec;

  padding: 10px 20px 10px 20px;

  cursor:pointer;
  font-family: 'Single Day', cursive;
  font-size: 18px;
}

/* DiaryItem 스타일링 */
.DiaryItem{
  padding: 15px 0px 15px 0px;
  
  border-bottom: 1px solid #e2e2e2;

  display: flex;
  justify-content: space-between;
}

.DiaryItem .emotion_img_wrapper{
  cursor: pointer;
  min-width :120px;
  height: 80px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
}

.DiaryItem .emotion_img_wrapper_1{
  background-color: #64C964;
}
.DiaryItem .emotion_img_wrapper_2{
  background-color: #9DD772;
}
.DiaryItem .emotion_img_wrapper_3{
  background-color: #FDCE17;
}
.DiaryItem .emotion_img_wrapper_4{
  background-color: #FD8446;
}
.DiaryItem .emotion_img_wrapper_5{
  background-color: #FD565F;
}

.DiaryItem .emotion_img_wrapper img{
  width: 50%;
}

.DiaryItem .info_wrapper{
  cursor: pointer;
  flex-grow: 1;
  margin-left: 20px;
}

.DiaryItem .diary_date{
  font-weight: bold;
  font-size: 25px;
  margin-bottom: 5px;
}

.DiaryItem .diary_content_preview{
  font-size: 18px;
}

.DiaryItem .btn_wrapper{
  min-width: 70px;
}

/* DiaryEditor 스타일링 */

.DiaryEditor{

}

.DiaryEditor h4{
  font-size: 22px;
  font-weight: bold;
}

.DiaryEditor .input_date{
  border:none;
  border-radius: 5px;
  background-color: #ececec;

  padding: 10px 20px 10px 20px;

  cursor:pointer;
  font-family: 'Single Day', cursive;
  font-size: 20px;
}

.DiaryEditor .emotion_list_wrapper{
  display: grid;
  grid-template-columns: repeat(5,auto);
  gap:2%;
}

.DiaryEditor textarea{
  font-family: 'Single Day', cursive;
  font-size: 20px;

  box-sizing: border-box;
  width: 100%;
  min-height: 200px;
  resize: vertical;

  border:none;
  border-radius: 5px;
  background-color: #ececec;

  padding:20px;
}

.DiaryEditor .control_box{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* EmotionItem 스타일링 */

.EmotionItem{
  cursor:pointer;

  border-radius: 5px;
  padding: 20px 0px 2px 0px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.EmotionItem img{
  width: 50%;
  margin-bottom: 10px;
}

.EmotionItem span{
  font-size: 18px;
}

.EmotionItem_off{
  background-color: #ececec;
}

.EmotionItem_on_1{
  background-color: #64C964;
  color:white;
}
.EmotionItem_on_2{
  background-color: #9DD772;
  color:white;
}
.EmotionItem_on_3{
  background-color: #FDCE17;
  color:white;
}
.EmotionItem_on_4{
  background-color: #FD8446;
  color:white;
}
.EmotionItem_on_5{
  background-color: #fd565f;
  color:white;
}

/* Diary 스타일링 */
.DiaryPage{

}

.DiaryPage section{
  width: 100%;
  margin-bottom: 100px;

  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.DiaryPage h4{
  font-size: 22px;
  font-weight: bold;
}

.DiaryPage .diary_img_wrapper{
  background-color: #ececec;
  width: 250px;
  height: 250px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.DiaryPage .emotion_descript{
  font-size: 25px;
  color: white;
}

.DiaryPage .diary_img_wrapper_1{
  background-color: #64C964;
}
.DiaryPage .diary_img_wrapper_2{
  background-color: #9DD772;
}
.DiaryPage .diary_img_wrapper_3{
  background-color: #FDCE17;
}
.DiaryPage .diary_img_wrapper_4{
  background-color: #FD8446;
}
.DiaryPage .diary_img_wrapper_5{
  background-color: #fd565f;
}

.DiaryPage .diary_content_wrapper{
  width:100%;
  background-color: #ececec;
  border-radius: 5px;
  word-break: keep-all;
  overflow-wrap: break-word;
}
.DiaryPage .diary_content_wrapper p{
  padding:20px;
  text-align: left;
  font-size: 20px;
  font-family: 'Black Han Sans', sans-serif;
  font-weight: 400;
  line-height: 2.5;
}